import React from "react";
import s from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatusWithHooks from "./ProfileStatusiWithHooks";
import userPhoto from "../../../assets/images/i.jpg";


const ProfileInfo = ({profile, updateStatus, status}) => {

    if (!profile) {
        return <Preloader/>;
    }

    return (
        <div>

            <div className={s.descriptionBlock}>
                <img src={profile.photos.large ? profile.photos.large : userPhoto} className={s.userPhoto} alt=""/>

                <ProfileStatusWithHooks updateStatus={updateStatus} status={status}/>
            </div>
        </div>
    )


}

export default ProfileInfo;