import React from "react";
import Profile from "./Profile";
import {connect} from "react-redux";
import {getUserProfile, updateStatus} from "../../redux/profile-reducer";
import {useParams} from "react-router-dom";
import {compose} from "redux";
import {getUserStatus} from "../../redux/profile-reducer";

export function withRouter(Children) {

    return (props) => {
        const match = {params: useParams()}
        return <Children {...props} match={match}/>
    }
}


class ProfileContainer extends React.Component {

    componentDidMount() {
        let userId = this.props.match.params.userId;
        if (!userId) {
            userId = this.props.authorizedUserId
            if (!userId) {
                this.props.history.push("/login");
            }
        }
        this.props.getUserProfile(userId)
        this.props.getUserStatus(userId)
    }

    render() {


        return (

            <Profile updateStatus={this.props.updateStatus} {...this.props} status={this.props.status} profile={this.props.profile}/>
        )
    }
}

let mapStateToPropsForRedirect = (state) => ({
    isAuth: state.auth.isAuth,
})

let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    authorizedUserId: state.auth.id,
    isAuth: state.auth.isAuth,
})

export default compose(
    withRouter,
    connect(mapStateToPropsForRedirect),
    connect(mapStateToProps, {
        getUserProfile,
        getUserStatus,
        updateStatus
    })
)(ProfileContainer);